nav {
    display: flex;
    background-color: rgb(90, 111, 117);
    justify-content: center;
    color: white;
}
h1 {
    font-size: 2.5rem;
    font-family: 'Times New Roman', Times, serif;
}
input {
    font-family: 'Roboto', sans-serif;
    color: #333;
    font-size: 1rem;
    margin: 0 auto;
    padding: 0.8rem 1.5rem;
    border-radius: 0.2rem;
    background-color: rgb(255, 255, 255);
    border: none;
    width: 90%;
    display: block;
    border-bottom: 0.3rem solid transparent;
}
.mainForm {
    display: flex;
    justify-content: center;
    height: 100%;
    width: 100%;
    background-color: rgba(90, 90, 97, 0.3);
}
section {
    padding: 20px;
    margin-top: 20px;
    width: 60%;
    border: 1px solid rgba(8, 8, 8,0.3) ;
}
.formTitle{
    font-size: 1.2rem;
    font-weight: bold;
    margin: 10px ;
}
.formInputArea{
    display: flex;
    flex-direction: column;
    row-gap: 10px;
   /* border: 1px solid red;
    */
}
#new--box{
    padding-top: 25px;
}
button{
    padding: 0.6rem;
    font-size: 18px;
    border-radius: 5px;
    font-weight: bold;
    background-color: rgb(51, 51, 51);
    color: rgb(238, 238, 238);
    margin: 15px;
    width: 97%;
    cursor: pointer;
}
#button--gen{
    background-color: rgb(47, 173, 21);
}
#button--reset{
    background-color: rgb(197, 81, 13);
}
.cv--section {
    display: flex;
    justify-content: center;
    height: 100%;
    width: 100%;
    background-color: rgba(90, 90, 97, 0.3);
}
.cv--data {
    background-color: white;
    overflow: hidden;
    padding: 20px;
}
#button--test {
    background-color: rgb(92, 90, 214);
}
/* --------------------------CV Template -------------------------------------- */
.clear {
    clear: both;
}
#page-wrap {
    width: 800px;
    margin: 40px auto 60px;
}
#pic {
    float: right;
    margin: -30px 0 0 0;
}
h1 {
    margin: 0 0 16px 0;
    padding: 0 0 16px 0;
    font-size: 42px;
    font-weight: bold;
    letter-spacing: -2px;
    border-bottom: 1px solid #999;
}
h2 {
    font-size: 20px;
    margin: 0 0 6px 0;
    position: relative;
}
h2 span {
    position: absolute;
    bottom: 0;
    right: 0;
    font-style: italic;
    font-family: Georgia, Serif;
    font-size: 16px;
    color: #999;
    font-weight: normal;
}
p {
    margin: 0 0 8px 0;
}
a {
    color: #999;
    text-decoration: none;
    border-bottom: 1px dotted #999;
}
a:hover {
    border-bottom-style: solid;
    color: black;
}
ul {
    margin: 0 0 32px 17px;
}
#objective {
    width: 500px;
    float: left;
}
#objective p {
    font-family: Georgia, Serif;
    font-style: italic;
    color: #666;
}
dt {
    font-style: italic;
    font-weight: bold;
    font-size: 18px;
    text-align: right;
    padding: 0 26px 0 0;
    width: 150px;
    float: left;
    height: 100px;
    border-right: 1px solid #999;
}
dd {
    width: 550px;
    float: right;
}
dd.clear {
    float: none;
    margin: 0;
    height: 15px;
}
